@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

*{
    padding: 0;
    margin: 0;
    font-family: 'Poppins', sans-serif;
}

.frame{
    width: 100vw;
    height: 100vh;
}

.main-container{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    
}

.frame-container{
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.buttons-container{
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.buttons-container button{
    margin-right: 50px;
    margin-left: 50px;
    font-size: 35px;
    font-weight: 700;
    font-family: 'Poppins', sans-serif;
    border: none;
    background-color: transparent;
}

.buttons-container button:hover{
    color: #A4A4A4;
    transition: 0.3s ease;
}

.buttons-container button:disabled{
    color: #A4A4A4;
}

.selected-item{
    color: #A4A4A4;
    font-size: 20px;
    margin-bottom: 30px;
}

.hours-container{
    display: grid;
    grid-template-columns: repeat(2, 230px);
    grid-gap: 50px;
}